export default class SchoolServices {
static async getSchoolById(id) {
  const response = await fetch(`https://u1206380.plsk.regruhosting.ru/api/schools/${id}`, {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      "Access-Control-Allow-Origin": "*",
    },
  });
  return response.json();
}
static async getCourseById(id) {
  const response = await fetch(`https://u1206380.plsk.regruhosting.ru/api/courses/${id}`, {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      "Access-Control-Allow-Origin": "*",
    },
  });
  return response.json();
}

static async searchCoursesBySchoolId(id, data) {
  const response = await fetch(`https://u1206380.plsk.regruhosting.ru/api/courses/search/5`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      "Access-Control-Allow-Origin": "*",
    },
    body: JSON.stringify(data)
  });
  const result = await response.json()
  return result;
}

}
